.main_div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo_list {
  min-width: 30%;
  list-style: none;
}

.todo {
  margin: .5rem;
  padding-left: .5rem;
  background: white;
  font-size: 1.2rem;
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 1s ease;
}

.todo li {
  flex: 1;
}

.trash_btn,
.complete_btn {
  background: #ff6f47;
  color: white;
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
}

.complete_btn {
  background: rgb(11, 212, 162);
}

.todo_item {
  padding: 0rem 0.5rem;
}

.fa-trash, .fa-check {
  pointer-events: none;
}

.fall {
  transform: translateY(10rem) rotateZ(20deg);
  opacity: 0;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}
